    <!-- 方法： 给整个 dialog 层添加点击事件触 closeDialog (拿到点击对象判断是否是 提示框中的 img) -->
    <div class="dialog" :style="{display: dialogDisplay}" @click="closeDialog">
      <!-- 点击弹出的适龄提示框 -->
      <div class="dialog-age">
        <img ref="ageMsg" src="@/assets/imgs/age.jpg" alt="">
