</template>

<script>
export default {
  data() {
    return {

      // dialog层 显示属性
      dialogDisplay: '',
      // 遮罩层 显示属性
      colorLayerDisplay: '',
      // social 折叠
      socialFold: true,
      // social 隐藏
      socialHidden: true,
      // news 区轮播图数据
      newsPic:[
        {id: 1, href: 'https://ys-test.ssr.mihoyo.com/news/detail/26325', src: 'https://webstatic.mihoyo.com/upload/contentweb/2023/02/23/a6ff91c08ee1bc0967567d97051cad76_4864960057670375541.jpg'},
        {id: 2, href: 'https://ys.mihoyo.com/main/news/detail/25700', src: 'https://webstatic.mihoyo.com/upload/contentweb/2023/01/13/1dacd9717c40f80729dbb0ce332498a9_7531663171871815960.png'},
        {id: 3, href: 'https://ys.mihoyo.com/main/news/detail/25597', src: 'https://webstatic.mihoyo.com/upload/contentweb/2023/01/06/825a567e4ad00369077dbfa3b1cf3655_7693927878137188563.jpg'},
        {id: 4, href: 'https://ys.mihoyo.com/main/news/detail/25063', src: 'https://webstatic.mihoyo.com/upload/contentweb/2022/12/09/1804282cf13c44711cf07ef6d23156fb_2308532683745949632.jpg'}, 
      ],
      // 小圆点个数
      sliderIndicator: [{ id: 1}, { id: 2}, { id: 3 }, { id: 4}],
      // news区轮播图  当前显示index
      currentIndex: 0,
      // news区轮播图 定时轮询
      newsTimer: null,

      // 新闻 tab 栏 当前index
