        { id: 2, cityName: '璃月港', bg: 'https://uploadstatic.mihoyo.com/contentweb/20200515/2020051511071844630.jpg', char: 'https://uploadstatic.mihoyo.com/contentweb/20210624/2021062410110987203.png'},
        { id: 3, cityName: '稻妻城', bg: 'https://uploadstatic.mihoyo.com/contentweb/20210720/2021072011394586258.jpg', char: 'https://uploadstatic.mihoyo.com/contentweb/20210719/2021071917013922761.png'},
        { id: 4, cityName: '须弥城', bg: 'https://webstatic.mihoyo.com/upload/contentweb/2022/08/15/b01e3edba8378a485bd2f8b2075e4e86_8655330006701981250.jpg', char: 'https://webstatic.mihoyo.com/upload/contentweb/2022/08/15/9ec9dfd134fa8701b8d25ed856303ea1_4280333980101063155.png'},
      ]
    }
  },

  methods: {
    // 点击提示框事件
    // 1. 显示 dialog 层
    // 2. 显示正常内容 和 dialog 中间 的 笼罩层(效果：显示提示层时，其余内容被灰色笼罩)
    openDialog() {
      this.dialogDisplay = 'block'
      this.colorLayerDisplay = 'block'
    },

    // 关闭提示
    closeDialog(e) {
      // 判断点击对象，排除掉想要排除的元素
      if (e.target !== this.$refs.ageMsg) {
        this.dialogDisplay = 'none'
        this.colorLayerDisplay = 'none'
      }
    },

    // 开启news区轮播图定时器
    startNwesInterval() {
      // 定时器应该先清除，在开启，防止生成多个定时器
      clearInterval(this.newsTimer)
      // 三秒间隔
      this.newsTimer = setInterval(() => {
        this.currentIndex++
        // index下一次 等于 数组长度时，证明下一次应该开始新的轮回，currentIndex 重置为0
        if (this.currentIndex === this.newsPic.length) {
          this.currentIndex = 0
        }
      },3000) 
    },
    // 点击控制小圆点
    changeSelect(index) {
      this.currentIndex = index
    },
    // 鼠标移入清除定时器，移除开始定时器
    changeNewsInterval(e, flag) {
      if (flag) {
        // console.log(`${e.target} in`);
        clearInterval(this.newsTimer)
      }
      else {
        // console.log(`${e.target} out`);
        this.startNwesInterval()
      }
    },

    // 新闻栏 tab 栏点击事件
    selectNews(e,index) {
      this.newsTabIndex = index
      // console.log(index);
    },

    // 点击折叠 social
    socialDisplay() {
      this.socialFold = !this.socialFold
    },

    // 页面滚动事件
    handleScroll() {
      // console.log(window.scrollY);
      this.socialHidden = window.scrollY < 300 ? true : false
    }
  },

  computed: {
    // newsPic 移动距离
    moveNews() {
      return -640*this.currentIndex
    }
  },

  mounted() {
    this.startNwesInterval()
    window.addEventListener('scroll', this.handleScroll)
  }
}
</script>

