      // 四宫格
      .download-group {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-content: space-between;

        img {
          width: 160px;
          height: 52px;
        }
      }
    }

    // 箭头动画部分
    .poster-arrows {
