<template>
  <div class="header-wrap">
    <!-- 头部区分三部分： 1.左侧logo  2.导航栏  3.成长关爱+登录 -->
    <!-- 左侧 -->
    <a href=":javascript;" class="header-logo" @click.prevent></a>
    
    <!-- 中间导航栏 -->
    <div @mouseleave="movecorrect">
      <ul class="header-nav"  > 
        <li class="header-navitem" v-for="item in pathArr" :key="item.index">
          <router-link :to="item.path" exact  @mouseenter.native="moveLine">{{ item.msg }}</router-link>
        </li>
        <!-- 高亮bar -->
        <div id="lineHight"></div>
      </ul>
    </div>
    
    <!-- 登录 -->
    <div class="header-right">
      <a href="https://jiazhang.mihoyo.com" target="_blank" class="header-love">
        <span>成长关爱系统</span>
        <img src="data:image/png;base64," alt="">
      </a>
      <div class="header-login">
        <button class="login-btn">
          <span>登 录</span>
          <img src="data:image/png;base64," alt="">
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',

  data() {
    return {
      pathArr: [
        { msg: '首 页', path: '/main/home'},
        { msg: '新 闻', path: '/main/news'},
        { msg: '角 色', path: '/main/roles'},
        { msg: '世 界', path: '/main/worlds'},
        { msg: '漫 画', path: '/main/manhua'},
        { msg: '社 区', path: '/main/community'}
      ]
    }
  },

  methods: {
    moveLine(e) {
      // 移动
      // lineHight.style.transform = `translate(${e.target.offsetLeft - 30}px)`
      lineHight.style.left = `${e.target.offsetLeft}px`
      
    },
    // 移动当鼠标移除导航栏时要自动处于当前路由对应的标签位置
    movecorrect() {
      const active = document.querySelector('.header-nav .router-link-active')
      // lineHight.style.transform = `translate(${active.offsetLeft - 30}px)`
      lineHight.style.left = `${active.offsetLeft}px`
    }
  }
}
</script>

<style lang="less" scoped>
  .header-wrap {
    position: fixed;
    top: 0;

    z-index: 10;
    display: flex;
    height: 66px;
    width: 100%;
    min-width: 1280px;
    background-color: rgba(17, 17, 17, 0.75);
    .header-logo {
      width: 317px;
      background: url('@/assets/imgs/logo.png');
    }


    .header-nav {
      position: relative;
      display: flex;
      margin-left: -35px;
      // flex-direction: row;
      justify-content: center;
      align-items: center;

      .header-navitem {
        margin: 0 30px;
        height: 100%;

        text-align: center;
        line-height: 66px;
        font-size: 17px;
      }

      .header-navitem a {
        cursor: default;
      }

      #lineHight {
        position: absolute;
        top: 0;
        left: 30px;

        width: 40px;
        height: 5px;
        background-color: #69e0ff;

        transition: 0.2s;
      }

      .header-navitem:hover > a,
      .router-link-active {
        text-shadow: 0 0 10px #69e0ff, 0 0 20px #69e0ff, 0 0 40px #69e0ff;
        color: #fff;
      }
    }


    .header-right {
      position: absolute;
      top: 0;
      right: 30px;
      height: 100%;

      display: flex;
      align-items: center;

      .header-love {
        display: flex;
        align-items: center;
        margin-right: 20px;
        font-size: 17px;
        &:hover {
          color: #fff;
          img {
            opacity: 1;
          }
        }

        img {
          width: 27px;
          height: 27px;
          margin-left: 18px;
          opacity: 0.7;
        }
        
      }

      .header-login {
        display: flex;
        align-items: center;
        padding: 0 10px;

        .login-btn {
          display: flex;
          align-items: center;

          background: none;
          border: none;
          outline: none;
          font-size: 17px;
          color: #ccc;
          cursor: pointer;
          &:hover {
          color: #fff;
            img {
              opacity: 1;
            }
          }

          img {
            width: 27px;
            height: 27px;
            margin-left: 18px;
            opacity: 0.7;
          }
        }
      }
    }
  }
</style>